import React from 'react';
import { NavLink } from 'react-router-dom';
import logo from './logo.png';

const Navigation = () => {
    return (
        <nav className='navbar navbar-expand-md sticky-top navbar-light bg-light border-bottom'>
            <div className="container">
                <NavLink to="/" className='navbar-brand'><img className='nav-logo' src={logo} alt="" /> BartekShop</NavLink>
                <button className='navbar-toggler' type='button' data-toggle='collapse' data-target='#navbarMenu' aria-controls='navbarText' aria-expanded='false' aria-label='Toggle navigation'>
                    <span className="navbar-toggler-icon"></span>
                </button>
                <div className="collapse navbar-collapse justify-content-end" id='navbarMenu'>
                    <ul className='navbar-nav navbar-right'>
                        <li className="nav-item"><NavLink to="/about" className='nav-link h5'>O sklepie</NavLink></li>
                        <li className="nav-item"><NavLink to="/services" className='nav-link h5'>Oferta</NavLink></li>
                        <li className="nav-item"><NavLink to="/contact" className='nav-link h5'>Kontakt</NavLink></li>
                    </ul>
                </div>
            </div>
        </nav>
    );
}

export default Navigation;